* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    --c0: #deebf7;
    --c1: #9ecae1;
    --c2: #3182bd;
}
::-webkit-scrollbar {
    width: 0;
}
.App {
    width: 100vw;
    height: 100vh;
    color: #eee;
    background-color: var(--c0);
    /* background-color: #111; */
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.5) inset;
    overflow-y: scroll;
}
.body {
    width: 100%;
    height: 100%;
    display: flex;
}
.body .left {
    width: 50%;
    height: 100%;
}
.body .right {
    width: 50%;
    height: 100%;
    padding: 10px;
}
.body .left .tabbar {
    position: relative;
    display: flex;
    margin-top: 5px;
    padding-left: 5px;
    border-bottom: 1px solid var(--c2);
    overflow: hidden;
}
.body .left .tabbar .tab {
    position: relative;
    width: fit-content;
    height: 40px;
    margin-right: 10px;
    padding: 0 20px;
    line-height: 35px;
    text-align: center;
    font-weight: bold;
    color: var(--c2);
    background-color: #fff;
    border: 1px solid var(--c1);
    border-bottom: 0;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    cursor: pointer;
}
.body .left .tabbar .tab.selected {
    color: #fff;
    background-color: var(--c2);
    border-bottom: none;
}
.body .left .tabbar .tab .close {
    transform: rotate(45deg);
    position: absolute;
    top: 0px;
    right: 0px;
    width: 15px;
    height: 15px;
    line-height: 13px;
    text-align: center;
    border-radius: 50%;
    transition: all 0.3s;
}
.body .left .tabbar .tab .close:hover {
    color: #111;
    background-color: #fff;
    cursor: pointer;
}
.body .left .tabbar .new {
    position: absolute;
    top: 10px;
    right: 5px;
    width: 20px;
    height: 20px;
    line-height: 16px;
    text-align: center;
    background-color: var(--c2);
    border: 1px solid #d3dce6;
    border-radius: 4px;
}
.body .left .tabbar .new:hover {
    filter: brightness(1.2);
    cursor: pointer;
}
.right .top {
    position: relative;
    height: calc(50% - 0px);
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    background: #fff;
}
.demoSelect {
    position: absolute;
    top: 10px;
    left: 10px;
    display: flex;
    align-items: center;
    z-index: 999;
}
.demoSelect .title {
    font-weight: bold;
    color: var(--c2);
    margin-right: 10px;
}
.demoSelect .dot {
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 18px;
    margin-right: 10px;
    font-size: 12px;
    color: var(--c2);
    background-color: #fff;
    border-radius: 50%;
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.8);
    cursor: pointer;
}
.demoSelect .dot:hover {
    color: #fff;
    background-color: var(--c1);
    box-shadow: none;
}
.demoSelect .dot.selected {
    color: #fff;
    background-color: var(--c2);
    box-shadow: none;
}
.right .center {
    position: relative;
    height: calc(50% - 15px);
}
.right .center .configSelect {
    position: absolute;
    top: 20px;
    left: 10px;
    z-index: 999;
    display: flex;
}
.right .center .configSelect .group {
    display: flex;
    align-items: center;
    margin-right: 20px;
}
.right .center .configSelect .group .label {
    margin-right: 10px;
    color: var(--c2);
    font-weight: bold;
}
.output {
    height: 100%;
    padding-top: 10px;
}
.output .label {
    margin-bottom: 10px;
    font-weight: bold;
    color: #222;
    font-family: 'Times New Roman', Times, serif;
}
.output .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    padding-top: 50px;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    background-color: #fff;
}
.right .bottom {
}
.btnBox {
    position: absolute;
    top: 10px;
    right: 10px;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btnBox > .btn {
    width: fit-content;
    height: 30px;
    line-height: 28px;
    padding: 0 20px;
    font-weight: bold;
    border-radius: 20px;
    cursor: pointer;
    transition: all 0.3s;
}
.btnBox .upload {
    color: var(--c2);
    background-color: var(--c0);
    margin-right: 10px;
}
.btnBox .upload:hover {
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2) inset;
}
.btnBox .shot {
    color: #eee;
    background-color: var(--c2);
}
.btnBox .shot:hover {
    box-shadow: 0 0 5px 0 rgba(255, 255, 255, 0.8) inset;
}
